.contact {
  margin-left: 100px;
  margin-right: 100px;

  @media (max-width: 900px) {
    margin-left: 50px;
    margin-right: 50px;
    text-align: center;
    .text-center {
      display: inline-block;
    }
  }
  @media (max-width: 650px) {
    margin-top: 15px;
    margin-left: 30px;
    margin-right: 30px;
    .text-center {
      display: inline-block;
    }
  }

  .social-link {
    @media (max-width: 900px) {
      font-size: 75px;
    }

    @media (max-width: 650px) {
      font-size: 50px;
    }
  }

  .social-link:hover {
    opacity: 0.5;
  }

  .letter {
    font-size: 100px;
    font-weight: 700;
    letter-spacing: -10px;
    mix-blend-mode: darken;
    line-height: 60%;
    opacity: 0.95;
    text-transform: uppercase;
    font-family: Lato, sans-serif;

    @media (max-width: 1200px) {
      letter-spacing: -10px;
      font-size: 90px;
    }

    @media (max-width: 900px) {
      letter-spacing: -10px;
      font-size: 85px;
    }

    @media (max-width: 650px) {
      letter-spacing: -10px;
      font-size: 70px;
    }
    @media (max-width: 500px) {
      letter-spacing: -8px;
      font-size: 60px;
    }
    @media (max-width: 400px) {
      font-size: 50px;
    }
  }
}
